<template>
  <div>
    <van-nav-bar title="添加密码" :left-arrow="true" fixed @click-left="onClickLeft"/>
    <form action="javascript:void(0)" method="post">
      <van-cell-group>
        <van-field v-model="form.name" border placeholder="请输入名称" label="名称" required/>
        <van-field
          v-model="form.apassword"
          border
          placeholder="请输入密码"
          type="password"
          label="密码"
          required
        />
        <van-field
          v-model="form.instructions"
          border
          placeholder="请输入密码所属"
          label="所属"
          required
        />
        <van-field
          readonly
          v-model="form.group"
          clickable
          label="分类"
          required
          placeholder="选择分类"
          @click="showPicker = true"
        />
      </van-cell-group>

      <van-button type="primary" @click="addPass" block style="margin-top:10px">添加</van-button>

      <van-popup v-model="showPicker" position="bottom">
        <van-picker
          show-toolbar
          :columns="datalist"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
    </form>
  </div>
</template>

<script>
/* eslint-disable */
import { addPassword, getAllGroup } from "@/api/api.js";
export default {
  data() {
    return {
      showPicker: false,
      form: {
        name: null,
        apassword: null,
        instructions: null,
        group: null
      },
      datalist: [],
      
    };
  },
  created() {
    getAllGroup()
      .then(res => {
        if (res.code == "success") {
          var list = res.message.map(item => {
            return item.gName;
          });
          this.mainGroupList = res.message;
          this.datalist = list;
        }
      })
      .catch(err => {
        this.$toast.fail(err);
      });
  },
  methods: {
    onConfirm(value, index) {
      this.form.group = value;
      this.showPicker = false;
      //   console.log(this.mainGroupList[index].id);
    },
    onClickLeft(){
        this.$router.go(-1)
    },
    addPass() {
      addPassword(this.form)
        .then(res => {
          if (res.code == "success") {
            this.$toast.success(res.message);
            this.form = {
              name: null,
              apassword: null,
              instructions: null,
              group: null
            };
          }
        })
        .catch(err => {
          this.$toast.fail(err);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
</style>